.NavBar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}
.NavBar-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.NavBar-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}
.NavBar-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.NavBar-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    gap: 3px;
  
}
.NavBar-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: #ff4141;
}
.NavBar-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.NavBar-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid#7a7a7a;
    border-radius: 25px;
    background-color: #515151;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;

}
.NavBar-login-cart button:active{
    background-color: #f3f3f3;
}
.NavBar-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background-color: red;
    color: white;
}